@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    /* --popover: 0 0% 100%;
    --popover-foreground: 222.2 47.4% 11.2%; */

    --background: 0 0% 100%;
    --foreground: 222.2 47.4% 11.2%;
    --muted: 210 40% 96.1%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 47.4% 11.2%;
    --primary: 222.2 47.4% 11.2%;
    --secondary: 210 40% 96.1%;
    --secondary-foreground: 222.2 47.4% 11.2%;
    --accent: 210 40% 96.1%;
    --accent-foreground: 222.2 47.4% 11.2%;
    --destructive: 0 100% 50%;
    --destructive-foreground: 210 40% 98%;
    --ring: 215 20.2% 65.1%;
    --radius: 0.5rem;
    --primary-foreground: 210 40% 98%;    
    --color-1: 0 100% 63%;
    --color-2: 270 100% 63%;
    --color-3: 210 100% 63%;
    --color-4: 195 100% 63%;
    --color-5: 90 100% 63%;

    --shiny-width: 100px;    
  }

  .dark {
    /* --popover: 224 71% 4%;
    --popover-foreground: 215 20.2% 65.1%; */

    --primary-foreground: 210 40% 98%;    
    --color-1: 0 100% 63%;
    --color-2: 270 100% 63%;
    --color-3: 210 100% 63%;
    --color-4: 195 100% 63%;
    --color-5: 90 100% 63%;    
    --background: 224 71% 4%;
    --foreground: 213 31% 91%;
    --muted: 223 47% 11%;
    --muted-foreground: 215.4 16.3% 56.9%;
    --accent: 216 34% 17%;
    --accent-foreground: 210 40% 98%;
    --border: 216 34% 17%;
    --input: 216 34% 17%;
    --card: 224 71% 4%;
    --card-foreground: 213 31% 91%;
    --primary: 210 40% 98%;
    --primary-foreground: 222.2 47.4% 1.2%;
    --secondary: 222.2 47.4% 11.2%;
    --secondary-foreground: 210 40% 98%;
    --destructive: 0 63% 31%;
    --destructive-foreground: 210 40% 98%;
    --ring: 216 34% 17%;

    --shiny-width: 100px;    
  }
}

@layer base {
  * {
    @apply border-border;
  }
  /* body {
    @apply font-sans antialiased bg-background text-foreground;
  } */
  body {
    font-family: 'Compacta', sans-serif;
    /* font-family: 'Compacta', 'Impact', 'Arial Black', sans-serif; */

  }
  @font-face {
    font-family: 'Compacta'; /* Font name */
    src: url('/src/assets/fonts/unicode.compacta.ttf'); /* Path to the TTF file */
    font-weight: normal; /* Optional: Specify font weight */
    font-style: normal; /* Optional: Specify font style */
  }  
}
